import {
  Avatar,
  Badge,
  TableBody,
  TableCell,
  TableRow,
} from "@windmill/react-ui";
import MainDrawer from "components/drawer/MainDrawer";
import ProductDrawer from "components/drawer/ProductDrawer";
import CheckBox from "components/form/CheckBox";
import DeleteModal from "components/Staypackages/DeleteStaypackages";
import EditDeleteButton from "components/table/EditDeleteButton";
import ShowHideButton from "components/table/ShowHideButton";
import defalutimage from "../../assets/img/forgot-password-office-dark.jpeg";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useState, useContext, useEffect, useRef } from "react";
import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showingTranslateValue } from "utils/translate";
import { useHistory, useLocation } from "react-router-dom";
import { SidebarContext } from "context/SidebarContext";
import moment from "moment";

const ProductTable = ({
  products,
  isCheck,
  setIsCheck,
  pagenumber,
  getAllStayPackagesList,
  wholeCount,
  lang,
}) => {
  const { toggleModal, toggleDrawer1, toggleDrawer, setIsDrawerOpen1 } =
    useContext(SidebarContext);
  const history = useHistory();
  const [serviceId, setserviceId] = useState("");
  const [title, settitle] = useState("");
  const handleClick = (e) => {
    const { id, checked } = e.target;
    console.log("id", id, checked);

    setIsCheck([...isCheck, id]);
    if (!checked) {
      setIsCheck(isCheck.filter((item) => item !== id));
    }
  };

  const handleModalOpen = (data) => {
    toggleModal();
    console.log("clicked---", data);
    setserviceId(data);
  };
  const handleUpdate1 = (data) => {
    history.replace(`/edit-stay-packages/${data.id}/${pagenumber}`);
  };

  const convertToAmPm = (time) => {
    const [hours, minutes] = time.split(":");
    const parsedHours = parseInt(hours, 10);
    const period = parsedHours >= 12 ? "PM" : "AM";
    const formattedHours = parsedHours % 12 === 0 ? 12 : parsedHours % 12;
    return `${formattedHours}:${minutes} ${period}`;
  };

  return (
    <>
      {isCheck?.length < 1 && (
        <DeleteModal
          getAllStayPackagesList={getAllStayPackagesList}
          id={serviceId}
          title={title}
        />
      )}

      <TableBody>
        {products?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.Property?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.Stay?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.days}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {convertToAmPm(properties.check_in_time)} -{" "}
                {convertToAmPm(properties.check_out_time)}
              </span>
            </TableCell>
            <TableCell className="">
              {properties?.Package_activities?.map((pro) => (
                <ul>
                  <li className="text-sm">{pro?.Activity?.name}</li>
                </ul>
              ))}
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.badge}</span>
            </TableCell>
            <TableCell className="">
              {properties?.Package_buffets?.map((bro) => (
                <ul>
                  <li className="text-sm">{bro?.Buffet?.name}</li>
                </ul>
              ))}
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {"Adult: " + properties?.split_base_total_with_gst}
              </span>
              <br />
              <span className="text-sm">
                {"Child: " + properties?.child_split_base_total_with_gst}
              </span>
              <br />
              <span className="text-sm">
                {"Addinatinal / Person: " +
                  properties?.additional_price_per_person}
              </span>
            </TableCell>
            {/* <TableCell>
                {properties.slots_availability > 0 ? (
                  <Badge type="success">{t(properties.slots_availability)}</Badge>
                ) : (
                  <Badge type="danger">{t(properties.slots_availability)}</Badge>
                )}
              </TableCell> */}
            {/* <TableCell>
            <table>
<tr>
  <th>Day</th>
  <th>Item</th>
  <th>Time</th>
</tr>
{properties?.Package_itineraries?.map((it)=> ( <tr>
  <td>{it?.day_1 ? 'Day 1':""}{it?.day_2 ? "Day 2" :""}</td>
  <td>{it?.name}</td>
  <td>{it?.timing}</td>
</tr>))}

</table>

            </TableCell> */}
            <TableCell className="">
              {properties?.Package_block_dates?.map((bro) => (
                <span className="text-sm">
                  {moment(bro?.date).format("MMMM Do YYYY")}
                  <br />
                </span>
              ))}
            </TableCell>
            {/* <TableCell>
              {properties?.Package_images?.map((img, index) => (
                <LazyImage key={index} src={img.image} alt="Office" />
              ))}
            </TableCell> */}
            <TableCell>
              <button
                onClick={() => handleUpdate1(properties)}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit1")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() =>
                  handleModalOpen(properties.id, "title", "product")
                }
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;


