import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/stayPackagesdata";
import ProductTable from "components/Staypackages/StaypackagesList";
import CheckBox from "components/form/CheckBox";
import useProductFilter from "hooks/useProductFilter";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import { Link } from "react-router-dom";
import { staypackagesList } from "services/Api";

const Products = () => {
  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    currentPage,
    searchText,
    category,
    handleSubmitForAll,
    sortedField,
    limitData,
  } = useContext(SidebarContext);

  const [pagenumber, setpagenumber] = useState(1);
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [isCheck, setIsCheck] = useState([]);
  const [AllStaypackagesDetailList, setAllStaypackagesDetailList] = useState(
    []
  );
  const [loading, setLoading] = useState(true);
  const [wholeCount, setwholeCount] = useState({});

  const handleSelectAll = () => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(propertiesformlocal.map((li) => li._id));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  console.log("productss", propertiesformlocal);
  const { serviceData } = useProductFilter(propertiesformlocal);

  useEffect(() => {
    getAllStayPackagesList(pagenumber);
  }, []);

  const getAllStayPackagesList = async (pagenumber, search = "") => {
    const staylist = await staypackagesList(pagenumber, search);
    console.log(staylist.data.data, "staypackagesList");
    setLoading(false);
    setAllStaypackagesDetailList(staylist?.data?.data);
    setwholeCount(staylist?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllStayPackagesList(e);
  };
  const handleSearch = (search) => {
    getAllStayPackagesList(pagenumber, search);
  };

  return (
    <>
      <PageTitle>{t("Stay Package Details")}</PageTitle>
      {/* <MainDrawer>
        <ProductDrawer id={serviceId} />
      </MainDrawer> */}
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to="/add-stay-packages">
                  {" "}
                  <Button
                    // onClick={toggleDrawer}
                    className="w-full rounded-md h-12"
                  >
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Stay Packages")}
                  </Button>{" "}
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{"s.no"}</TableCell>
                <TableCell>{t("Property")}</TableCell>
                <TableCell>{t("Stay type")}</TableCell>
                <TableCell>{t("Package Name")}</TableCell>
                <TableCell>{t("Days")}</TableCell>
                <TableCell>{t("Check In/Out Timing")}</TableCell>
                <TableCell>{t("Activities")}</TableCell>
                <TableCell>{t("Badge")}</TableCell>
                <TableCell>{t("Buffets")}</TableCell>
                <TableCell>{t("Price")}</TableCell>
                <TableCell>{t("Blocked Date")}</TableCell>
                {/* <TableCell>{t("AVAILABILITY")}</TableCell> */}
                {/* <TableCell>{t("Internary")}</TableCell> */}
                {/* <TableCell
                  className="text-center"
                  width={"350px"}
                  style={{ display: "block" }}
                >
                  {t("Image")}
                </TableCell> */}
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              isCheck={isCheck}
              pagenumber={pagenumber}
              products={AllStaypackagesDetailList?.stayPackages}
              setIsCheck={setIsCheck}
              getAllStayPackagesList={getAllStayPackagesList}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={AllStaypackagesDetailList?.pagination?.total}
              resultsPerPage={AllStaypackagesDetailList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
